___CSS_LOADER_EXPORT___.push([module.id, "\n.selected-type {\n        background-color: transparent;\n        border-radius: 4px;\n        border: 1px solid #BCBCBC;\n        color: #212121;\n        padding-left: 12px;\n}\n.custom-placeholder:empty:before {\n        color:#BCBCBC !important;\n        font-weight: 400;\n}\n.custom-select .items .option {\n        font-size: 14px;\n}\n.custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/giveUsFeedbackDialog.vue"],"names":[],"mappings":";AA6II;QACI,6BAA6B;QAC7B,kBAAkB;QAClB,yBAAyB;QACzB,cAAc;QACd,kBAAkB;AACtB;AACA;QACI,wBAAwB;QACxB,gBAAgB;AACpB;AACA;QACI,eAAe;AACnB;AACA;QACI,yBAAyB;QACzB,cAAc;AAClB","sourcesContent":["<template>\n    <div class=\"container\">\n        <div class=\"label-div\">\n            <label class=\"type-label\">Feedback Type</label>\n            <CustomSelect :options=\"feedbackTypes\" :selectedClass=\"'selected-type'\" :placeholder=\"'Select'\" class=\"select-type\"></CustomSelect>\n        </div>\n        <div class=\"label-div\">\n            <label class=\"type-label\">Description</label>\n            <textarea class=\"description-text\" :placeholder=\"'What would you like to say to us?'\"></textarea>\n        </div>\n        <div class=\"label-div\">\n            <label class=\"type-label\">Attachments</label>\n            <input type=\"file\" name=\"attachment\" id=\"attachment\" class=\"hide-default-input\" @change=\"getFileNames()\" multiple/>\n            <label for=\"attachment\" class=\"upload-files\">\n                <i class=\"mdi mdi-cloud-upload-outline\" style=\"font-size: 20px;color: #BCBCBC;margin-right: 8px;\"></i>\n                <span style=\"font-weight: 500\">Click</span> here to upload your files\n            </label>\n        </div>\n        <!-- <div class=\"label-div\">\n            <div v-for=\"file in uploadedFiles\">1. {{ file.name }}</div>\n        </div> -->\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button id=\"sendButton\" class=\"submit-feedback-button\" @click=\"createFeedback()\">SUBMIT</button>\n        </div>\n    </div>\n</template>\n<script setup lang=\"ts\">\n    import { container } from \"../../../../domain/core/diContainer\";\n    import { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\n    import { IUserUseCase } from \"../../../../domain/useCase/IUserUseCase\";\n    import { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\n    import CustomSelect from \"../customSelect.vue\"\n\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const userUseCase = container.resolve<IUserUseCase>(IUserUseCase);\n\n    const feedbackTypes = [\"I have an idea\", \"I have a problem\", \"I have a question\"];\n    const emit = defineEmits([\"close\"]);\n    var uploadedFiles: Array<unknown> = [];\n\n    function close() {\n        emit(\"close\");\n    }\n    function getFileNames () {\n        let files = (document.getElementById(\"attachment\")! as any).files;\n        uploadedFiles = files;\n        console.log(\"files => \", uploadedFiles);\n        return uploadedFiles;\n    }\n\n    async function createFeedback() {\n        executor.execute(async () => {\n            await userUseCase.createFeedback({type: \"Type Test\", description: \"Description Test\" });\n        }, { loading: true });\n        emit(\"close\");\n    }\n</script>\n\n<style scoped>\n    .container {\n        padding: 32px 20px 20px;\n    }\n    .label-div {\n        margin-top: 20px;\n    }\n    .type-label {\n        font-weight: 500;\n        font-size: 15px;\n        line-height: 18px;\n        color: #151233;\n        margin-bottom: 4px;\n    }\n    .select-type {\n        width: 100%;\n        font-size: 15px;\n        height: 40px;\n        line-height: 40px;\n    }\n    .description-text {\n        width: 100%;\n        height: 120px;\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        color: #212121;\n        resize: none;\n        padding: 11px 12px;\n\n    }\n    textarea:focus {\n        outline: none;\n    }\n    .hide-default-input {\n        width: 0.1px;\n        height: 0.1px;\n        opacity: 0;\n        overflow: hidden;\n        position: absolute;\n        z-index: -1;\n    }\n    .upload-files{\n        width: 100%;\n        padding: 31px 135px;\n        border: 1px dashed #BCBCBC;\n        border-radius: 4px;\n        font-size: 15px;\n        cursor: pointer;\n    }\n    .buttons-div {\n        display: flex;\n        justify-content: end;\n        margin-top: 52px;\n    }\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #757575;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        margin-right: 4px;\n        border: none;\n        background: transparent;\n    }\n    .submit-feedback-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #FFFFFF;\n        background: #151233;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        border: none;\n    }\n</style>\n\n<style>\n    .selected-type {\n        background-color: transparent;\n        border-radius: 4px;\n        border: 1px solid #BCBCBC;\n        color: #212121;\n        padding-left: 12px;\n    }\n    .custom-placeholder:empty:before {\n        color:#BCBCBC !important;\n        font-weight: 400;\n    }\n    .custom-select .items .option {\n        font-size: 14px;\n    }\n    .custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n    }\n</style>"],"sourceRoot":""}]);
